import { CITIES, IMAGES, OFFERS, TYPE_POINTS } from '../const.js';
import { createElement } from '../render.js';
import { getFullDate } from '../utils.js';

function createEditablePointTemplate(point = {}) {
  const {
    type = '',
    destination = { name: '', description: '', pictures: [] },
    dateFrom = '',
    dateTo = '',
    basePrice = '',
    offers = [],
    description = ''
  } = point;

  return `<li class="trip-events__item">
   <form class="event event--edit" action="#" method="post">
     <header class="event__header">
       <div class="event__type-wrapper">
         <label class="event__type event__type-btn" for="event-type-toggle-1">
           <span class="visually-hidden">Choose event type</span>
           <img class="event__type-icon" width="17" height="17" src="img/icons/${type || 'default'}.png" alt="Event type icon">
         </label>
         <input class="event__type-toggle visually-hidden" id="event-type-toggle-1" type="checkbox">
         <div class="event__type-list">
           <fieldset class="event__type-group">
             <legend class="visually-hidden">Event type</legend>
             ${TYPE_POINTS.map((item) => `
               <div class="event__type-item">
                 <input id="event-type-${item.toLowerCase()}-1" class="event__${item.toLowerCase()}-input visually-hidden" type="radio" name="event-type" value="${item.toLowerCase()}" ${item === type ? 'checked' : ''}>
                 <label class="event__type-label event__type-label--${item.toLowerCase()}" for="event-type-${item.toLowerCase()}-1">${item}</label>
               </div>
             `).join('')}
           </fieldset>
         </div>
       </div>
       <div class="event__field-group event__field-group--destination">
         <label class="event__label event__type-output" for="event-destination-1">
           ${type || 'Type'}
         </label>
         <input class="event__input event__input--destination" id="event-destination-1" type="text" name="event-destination" value="${destination.name || ''}" list="destination-list-1">
         <datalist id="destination-list-1">
           ${CITIES.map((town) => `<option value="${town.name}"></option>`).join('')}
         </datalist>
       </div>
       <div class="event__field-group event__field-group--time">
         <label class="visually-hidden" for="event-start-time-1">From</label>
         <input class="event__input event__input--time" id="event-start-time-1" type="text" name="event-start-time" value="${dateFrom ? getFullDate(dateFrom) : ''}">
         &mdash;
         <label class="visually-hidden" for="event-end-time-1">To</label>
         <input class="event__input event__input--time" id="event-end-time-1" type="text" name="event-end-time" value="${dateTo ? getFullDate(dateTo) : ''}">
       </div>
       <div class="event__field-group event__field-group--price">
         <label class="event__label" for="event-price-1">
           <span class="visually-hidden">Price</span>
           &euro;
         </label>
         <input class="event__input event__input--price" id="event-price-1" type="text" name="event-price" value="${basePrice}">
       </div>
       <button class="event__save-btn btn btn--blue" type="submit">Save</button>
       <button class="event__reset-btn" type="reset">Cancel</button>
     </header>
     <section class="event__details">
       <section class="event__section event__section--offers">
         <h3 class="event__section-title event__section-title--offers">Offers</h3>
         <div class="event__available-offers">
         ${OFFERS.map((offer) => `
           <div class="event__offer-selector">
             <input class="event__offer-checkbox visually-hidden" id="event-offer-${offer.title.toLowerCase()}-1" type="checkbox" name="event-offer-${offer.title.toLowerCase()}" ${isChecked(offers, offer)}>
             <label class="event__offer-label" for="event-offer-${offer.title.toLowerCase()}-1">
               <span class="event__offer-title">${offer.title}</span>
               &plus;&euro;&nbsp;
               <span class="event__offer-price">${offer.price}</span>
             </label>
           </div>
         `).join('')}
         </div>
       </section>
       <section class="event__section event__section--destination">
         <h3 class="event__section-title event__section-title--destination">Destination</h3>
         <p class="event__destination-description">${destination.description || description || ''}</p>
         <div class="event__photos-container">
           <div class="event__photos-tape">
           ${IMAGES.map((img) => `<img class="event__photo" src="${img}.jpg" alt="Event photo">`).join('')}
           </div>
         </div>
       </section>
     </section>
   </form>
 </li>`;
}

function isChecked(offers, offerToCheck) {
  let check = false;
  offers.forEach((offer) => {
    if (offer.title === offerToCheck.title) {
      check = true;
    }
  });
  return check ? 'checked' : '';
}

export default class CurrentFormView {
  constructor({ point } = {}) {
    // Если данные не переданы, задаём пустой объект с дефолтными значениями
    this.point = point || {
      type: '',
      destination: { name: '', description: '', pictures: [] },
      dateFrom: '',
      dateTo: '',
      basePrice: '',
      offers: [],
      isFavourite: false,
      description: ''
    };
  }

  getTemplate() {
    return createEditablePointTemplate(this.point);
  }

  getElement() {
    if (!this.element) {
      this.element = createElement(this.getTemplate());
    }
    return this.element;
  }

  removeElement() {
    this.element = null;
  }
}
